import { defineStore } from 'pinia'

export const useThemeStore = defineStore('theme', {
  state: () => ({
    theme: 'light',
    systemDarkMode: false,
    isAuto: true // 新增自动模式状态
  }),
  actions: {
    setTheme(theme) {
      this.theme = theme
      this.isAuto = false // 手动设置时关闭自动模式
      localStorage.setItem('theme', theme)
    },
    initSystemListener() {
      // 系统主题匹配器
      const darkModeMatcher = window.matchMedia('(prefers-color-scheme: dark)')
      
      // 更新系统状态
      const updateSystemState = () => {
        this.systemDarkMode = darkModeMatcher.matches
        if (this.isAuto) {
          this.theme = this.systemDarkMode ? 'dark' : 'light'
          localStorage.removeItem('theme') // 自动模式不存储用户偏好
        }
      }

      // 初始设置
      updateSystemState()
      
      // 监听系统主题变化
      darkModeMatcher.addEventListener('change', updateSystemState)
    }
  }
})
