<template>
  <div class="main-container" :class="{ 'dark-theme': isDarkTheme }">
    <h1 id="page-title">DeepSeek-R1 本地模型WebUI</h1>

    <div id="main">
      <div id="chat-container">
        <div v-for="(message, index) in messages" :key="index" 
             :class="['message', message.role + '-message']">
          <div v-if="message.role === 'bot'" class="bot-message">
            <div class="bot-message-header">模型:</div>
            <div class="bot-message-content">{{ message.content }}</div>
          </div>
          <div v-else>{{ message.content }}</div>
        </div>
        <div v-if="isGenerating" class="message loading-indicator">
          <div class="loading-spinner"></div>
          <span>正在生成内容...</span>
        </div>
      </div>

      <div id="input-container">
        <textarea id="input-text" v-model="userInput" rows="3" 
                  placeholder="请输入您的文本" @keyup.enter="sendMessage"></textarea>
        <button id="submit-btn" @click="sendMessage" :disabled="isGenerating">发送</button>
      </div>
      
      <div id="ai-warning">本系统生成内容由AI大模型提供，可能存在不准确信息，请谨慎使用</div>
    </div>

    <a href="https://github.com/WavesMan/Windows-Tools" class="github-link" target="_blank">
      <i class="fa-brands fa-github"></i>
    </a>

    <button id="theme-toggle-btn" @click="toggleTheme">
      <i class="fa-solid fa-circle-half-stroke"></i>
    </button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      isDarkTheme: false,
      isGenerating: false,
      userInput: '',
      messages: []
    }
  },
  methods: {
    async sendMessage() {
      if (this.isGenerating || !this.userInput.trim()) return;

      this.isGenerating = true;
      this.messages.push({ role: 'user', content: this.userInput });
      const userMessage = this.userInput;
      this.userInput = '';

      try {
        const response = await fetch('/api/chat', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/x-ndjson'
          },
          body: JSON.stringify({ message: userMessage })
        });

        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);

        const botMessage = { role: 'bot', content: '' };
        this.messages.push(botMessage);

        const reader = response.body.getReader();
        const decoder = new TextDecoder();
        let buffer = '';

        while (true) {
          const { done, value } = await reader.read();
          if (done) break;

          buffer += decoder.decode(value, { stream: true });
          const chunks = buffer.split('\n');
          
          chunks.slice(0, -1).forEach(chunkStr => {
            try {
              const chunk = JSON.parse(chunkStr);
              if (chunk.error) {
                botMessage.content = `错误: ${chunk.error}`;
              } else if (chunk.is_end) {
                console.log(`生成完成，耗时: ${chunk.metrics?.time_cost?.toFixed(2)}秒`);
              } else {
                botMessage.content += chunk.content;
              }
            } catch (e) {
              console.error('解析错误:', e);
            }
          });
          
          buffer = chunks[chunks.length - 1];
          this.$forceUpdate(); // 强制更新视图以显示流式内容
        }
      } catch (error) {
        this.messages.push({ role: 'bot', content: `错误: ${error.message}` });
      } finally {
        this.isGenerating = false;
      }
    },
    toggleTheme() {
      this.isDarkTheme = !this.isDarkTheme;
    }
  }
}
</script>

