<script setup>
import { ref, onMounted } from 'vue' // 移除未使用的 onBeforeUnmount
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from '@fortawesome/free-solid-svg-icons'
import { far } from '@fortawesome/free-regular-svg-icons'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { useThemeStore } from '@/store/theme'

library.add(fas, far, fab)

const themeStore = useThemeStore()
const currentTheme = ref('light')

// 初始化主题
onMounted(() => {
  // 初始化系统监听
  themeStore.initSystemListener()
  
  // 优先读取本地存储，其次用系统主题
  const savedTheme = localStorage.getItem('theme')
  if (savedTheme) {
    currentTheme.value = savedTheme
    themeStore.isAuto = false
  } else {
    currentTheme.value = themeStore.systemDarkMode ? 'dark' : 'light'
    themeStore.isAuto = true
  }

  applyTheme(currentTheme.value)
})

// 主题切换逻辑
const toggleTheme = () => {
  let newTheme
  if (themeStore.isAuto) {
    // 从自动模式切换到手动模式
    newTheme = themeStore.systemDarkMode ? 'dark' : 'light'
    themeStore.isAuto = false
  } else {
    // 手动切换
    newTheme = currentTheme.value === 'light' ? 'dark' : 'light'
  }
  
  currentTheme.value = newTheme
  themeStore.setTheme(newTheme)
  applyTheme(newTheme)
}

// 应用主题到DOM
const applyTheme = (theme) => {
  document.documentElement.setAttribute('data-theme', theme)
  localStorage.setItem('theme', theme)
}
</script>

<template>
  <div id="app">
    <nav>
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link> 
    </nav>
    <router-view/>
    
    <div 
      id="ThemeSwitcher"
      @click="toggleTheme"
      :title="currentTheme === 'light' ? '切换深色模式' : '切换浅色模式'"
    >
      <font-awesome-icon 
        :icon="['fas', 'circle-half-stroke']" 
        :class="{ 'dark-mode-icon': currentTheme === 'dark' }"
      />
    </div>
  </div>
</template>

<style scoped>
